/* ============
 * Mutations for the auth module
 * ============
 *
 * The mutations that are available on the
 * account module.
 */

import Vue from 'vue'
import {
  CHECK,
  REGISTER,
  LOGIN,
  LOGOUT
} from './mutation-types'

export default {

  [CHECK] (state) {
    state.authenticated = !!localStorage.getItem('star_token')
    if (state.authenticated) {
      var token = JSON.parse(localStorage.getItem('star_token'))
      Vue.$http.defaults.headers.common.Authorization = `Bearer ${token.access_token}`
    }
  },

  [REGISTER] () {
    //
  },

  [LOGIN] (state, token) {
    state.authenticated = true
    localStorage.setItem('star_token', JSON.stringify(token))
    Vue.$http.defaults.headers.common.Authorization = `Bearer ${token.access_token}`
  },

  [LOGOUT] (state) {
    state.authenticated = false
    localStorage.removeItem('star_token')
    Vue.$http.defaults.headers.common.Authorization = ''
  }
}



