import Vue from 'vue'
import store from '@/store'
import Proxy from '@/proxies/AuthProxy'
import * as types from './mutation-types'
import {
  MessageBox
} from 'element-ui'

export const check = ({
  commit
}) => {
  commit(types.CHECK)
}

export const register = ({
  commit
}) => {
  /*
   * Normally you would use a proxy to register the user:
   *
   * new Proxy()
   *  .register(payload)
   *  .then((response) => {
   *    commit(types.REGISTER, response);
   *  })
   *  .catch(() => {
   *    console.log('Request failed...');
   *  });
   */
  commit(types.LOGIN, 'RandomGeneratedToken')
  Vue.router.push({
    name: 'home'
  })
}

export const login = ({
  commit
}, payload) => {
  new Proxy()
    .login(payload)
    .then((response) => {
      commit(types.LOGIN, response)
      store.dispatch('account/find')
      Vue.router.push({
        name: 'home'
      })
    })
    .catch((error) => {
      MessageBox({
        type: 'error',
        message: error.message
      })
    })
}

export const logout = ({
  commit
}) => {
  commit(types.LOGOUT)
  Vue.router.replace({
    name: 'login'
  })
}

export default {
  check,
  register,
  login,
  logout
}



