export default [
  {
    path: '/home',
    name: 'home',
    component: () => import('@/pages/Hello'),
    meta: {
      auth: true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/pages/Login'),
    meta: {
      guest: true
    }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('@/pages/Register'),
    meta: {
      guest: true
    }
  },
  {
    path: '/',
    redirect: '/home'
  },

  {
    path: '/*',
    redirect: '/home'
  }
]



