import * as types from './mutation-types'
import Proxy from '@/proxies/AccountProxy'
import {
  MessageBox
} from 'element-ui'

export const find = ({
  commit
}) => {
  new Proxy()
    .me()
    .then((response) => {
      window.console.log(response)
      commit(types.FIND, response)
    })
    .catch((error) => {
      MessageBox({
        type: 'error',
        message: error.message
      })
    })

  // const account = {
  //   firstName: 'John',
  //   lastName: 'Doe',
  //   email: 'jonh@doe.com'
  // }
  // commit(types.FIND, account)
}

export default {
  find
}



